<template>
  <div style="width:100%">
    <!-- 列表 -->
    <el-table :data="tableData" border >
      <el-table-column header-align="center" align="center" label="序号"  min-width="50">
         <template slot-scope="scope">
	          <span>{{ scope.row.id }}</span>
	        </template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="知识库数据类型名称"  >
				<template slot-scope="scope">
					<span>{{scope.row.knowledgeTypeName}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="值"  >
				<template slot-scope="scope">
					<span>{{scope.row.knowledgeVaule}}</span>
				</template>
			</el-table-column>
			<el-table-column header-align="center" align="center" label="创建时间"  >
				<template slot-scope="scope">
					<span>{{scope.row.create_time}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="创建人"  >
				<template slot-scope="scope">
					<span>{{scope.row.creater}}</span>
				</template>
			</el-table-column>
      <el-table-column header-align="center" align="center" label="状态"  >
				<template slot-scope="scope">
					<span>{{scope.row.status}}</span>
				</template>
			</el-table-column>
		</el-table>
    <!-- 底部toolbar -->
			<el-col :span="24" class="toolbar">
				<el-pagination layout="prev, pager, next, jumper" @current-change="currentChange" :page-size="10" :total="queryStr.total" style="float:right;">
				</el-pagination>
			</el-col>
   
  </div>
</template>
<script>
  import {carTypeList,addCarTypes} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data(){
      return{
        queryStr: {
          bus_rand:'',
          bus_type:'',
          bus_series:'',
          total: 1,       // 总数
          page: 1,        // 当前分页
          size: 10,      // 每页条数
        },
        form_one_data:{
				 bus_rand:"",
				 bus_type:"",
         bus_series:"",
         status:'1',
        },
        dialogFormOneVisible: false,
        dialogTableVisible: false,  
        tableData:[{
          id:'1',
          knowledgeTypeName:"燃油类型",
          knowledgeVaule:"1",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },{
          id:'2',
          knowledgeTypeName:"驱动形式",
          knowledgeVaule:"2",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },{
          id:'3',
          knowledgeTypeName:"车辆颜色",
          knowledgeVaule:"3",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },{
          id:'4',
          knowledgeTypeName:"准驾车型",
          knowledgeVaule:"4",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },{
          id:'5',
          knowledgeTypeName:"供应商类型",
          knowledgeVaule:"5",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },{
          id:'6',
          knowledgeTypeName:"保险种类",
          knowledgeVaule:"6",
          create_time:"2018-10-11",
          creater:"Admin",
          status:"正常"
        },
        ],
        tableTwoData:[],
        loading: false,//loading
        centerDialogVisible: false,
        quesData:{
            phoneNumber: '',
            issueContent: '',
            issueTypeId: 1,
            status: 1,
            result : ''
        },
      }
    },
    mounted(){
    
    
    },
    methods:{
      // 查询
      query(run_type) { 
          let params = {
              page: this.queryStr.page,
              limit: this.queryStr.limit,
              bus_brand: this.queryStr.bus_brand,
              bus_type: this.queryStr.bus_type,
              bus_series:this.queryStr.bus_series
          };
          carTypeList(params).then((res) => {
            console.log(111)
            this.total = Number(res.data.data.total);
            this.tableData = res.data.data.data;
            console.log(this.tableData)
          });
        
      },
      //新建品牌车型
      addCarType() {
          let params = {
              bus_brand:this.form_one_data.bus_brand,
              bus_series: this.form_one_data.bus_series,
              bus_type: this.form_one_data.bus_type,
              status:this.form_one_data.status,
          };
          console.log(params)
          addCarTypes(params).then((res) => {
              this.query();
          });
      },
      resetForm(form){
        this.$refs['form1'].resetFields();
      },
      putAction(){
        this.$router.push({
          path: '/exportInfo',
        })
      },
      addAction(){
         this.$router.push({
          path: '/creditInformation',
        })
      },
      // 分页器
      currentChange(val) {
				this.queryStr.page = val;
				this.query();
			},
      // 提交
      submit_query(){
        this.$refs['form'].validate((valid) =>{
          this.flag = true;
          if (!valid) {
            this.flag = false;
            return false;
          }else{
            if(this.query_dialog_type == 0){
              issues_add(this.quesData).then(res => {
                this.centerDialogVisible = false;
                this.flag = false;
                // this.start_end_time = [(new Date().getTime() - 60*1000), new Date().getTime()]
              })
            }else{
              issues_edit(this.quesData).then(res => {
                this.centerDialogVisible = false;
                this.tableData[this.list_active_index] = Object.assign(this.tableData[this.list_active_index], this.quesData);
                this.flag = false;
              })
            }
            this.query();
          }
        })
      },
      // 添加
      add_query(){
        this.$refs['form'].resetFields();
        this.query_dialog_type = 0;
        this.centerDialogVisible = true;
      },
      // 修改 yjq
      edit_query(row, index){
  
      },
      // 时间戳的转换
      changeTime(time){
          let year = new Date((time)).getFullYear();
          let month = new Date((time)).getMonth()+1 < 10?'0'+(new Date((time)).getMonth()+1):(new Date((time)).getMonth()+1);
          let date = new Date((time)).getDate() < 10 ?'0'+new Date((time)).getDate():new Date((time)).getDate();
          let hours = new Date((time)).getHours() < 10 ?'0'+new Date((time)).getHours():new Date((time)).getHours();
          let minutes = new Date((time)).getMinutes() < 10 ?'0'+new Date((time)).getMinutes():new Date((time)).getMinutes();
          let seconds = new Date((time)).getSeconds() < 10 ?'0'+new Date((time)).getSeconds():new Date((time)).getSeconds();
          return year+'-'+month+'-'+date+' '+hours+':'+minutes+':'+seconds
      }
    }
  }
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}

  
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





